import { hasSpecificDirectChildTag, HEADING_TAGS } from '../../utils';
export const HEADING_COLORS = ['primary', 'inherit'];
const headingSizeToTagMap = {
    small: 'h6',
    medium: 'h5',
    large: 'h4',
    'x-large': 'h3',
    'xx-large': 'h2',
    inherit: 'h2',
};
export const getHeadingTagType = (host, size, tag) => {
    if (hasSpecificDirectChildTag(host, HEADING_TAGS.join())) {
        return 'div';
    }
    else if (tag) {
        return tag;
    }
    else {
        return headingSizeToTagMap[size] || 'h2';
    }
};
